.DuncanIdaho {
  -webkit-animation: rotating 1s linear infinite;
  animation: rotating 1s linear infinite;
  position: relative;
  /* added this part */
  visibility: hidden;
}

.DuncanIdaho.attacking{
  position: relative;
  -webkit-animation-name: attack;
  -webkit-animation-duration: 800ms;
  animation-name: attack;
  -webkit-animation-duration: 800;
          animation-duration: 800;
  visibility: visible;
 }
@-webkit-keyframes attack {
  0%   { left: 0px;;}

  50%  { left: 190%;}

  100% { left: 0%;}
}

.Grunt {
  -webkit-animation: rotating 1s linear infinite;
  animation: rotating 1s linear infinite;
  position: relative;
  /* setting attacking frame to not be visible normally */
  visibility: hidden;
}

.Grunt.attacking{
  position: relative;
  -webkit-animation-name: attackGrunt;
  -webkit-animation-duration: 800ms;
  animation-name: attackGrunt;
  -webkit-animation-duration: 800;
          animation-duration: 800;
  /* setting attacking frame to be visible upon state change */
  visibility: visible;
 }

